.Slider1 {
  height: 533px;
  overflow: hidden;
}

.Slider1 figure {
  width: 500%;
  position: relative;
  padding: 0;
  margin: 0;
  -webkit-animation: 20s Slider1 infinite;
          animation: 20s Slider1 infinite;
}

.Slider1 div {
  width: 20%;
  float: left;
  background-color: #f2f2f2;
}

@-webkit-keyframes Slider1 {
  0% {
    left: 0;
  }

  50% {
    left: -100%;
  }

  100% {
    left: -200%;
  }
}

@keyframes Slider1 {
  0% {
    left: 0;
  }

  50% {
    left: -100%;
  }

  100% {
    left: -200%;
  }
}





